  <div class="Menu">

    <!-- Áî® 0 ÂºÄÂßãÊ†áËÆ∞ÊòØÊï∞Â≠ó -->
    <van-cell @click="memory('0' + a1,a2 - a1)" title="Êï∞Â≠óËÆ∞ÂøÜ" label="Number Memory"/>

    <van-cell @click="memory('A', 26)" title="Â§ßÂÜôÂ≠óÊØçËÆ∞ÂøÜ" label="Upper Case Letters Memory"/>
    <van-cell @click="memory('a', 26)" title="Â∞èÂÜôÂ≠óÊØçËÆ∞ÂøÜ" label="Lower Case Letters Memory"/>

    <!-- https://www.unicode.org/charts/charindex.html#J-->
    <van-cell @click="memory('„ÅÅ', 86)" title="Âπ≥ÂÅáÂêçËÆ∞ÂøÜ" label="Japanese Hiragana Memory"/>
    <van-cell @click="memory('„Ç°', 95)" title="ÁâáÂÅáÂêçËÆ∞ÂøÜ" label="Japanese Katakana Memory"/>

    <!-- https://www.emojiall.com/ -->
    <!-- https://www.qqxiuzi.cn/zh/emoji.html -->
    <van-cell @click="memory('üê≠', 17)" title="Âä®Áâ©ÂõæÂΩ¢ËÆ∞ÂøÜ" label="Animal Memory"/>
    <van-cell @click="memory('üé†', 52)" title="Á∫¶‰ºöÂõæÂΩ¢ËÆ∞ÂøÜ" label="Dating Event Memory"/>

    <van-cell center title="Ë°åÂàóÊï∞" label="Row and Column" value="">
      <van-row>
        <van-col span="12">
          <van-stepper v-model="row" :min="1" :button-size="bottonSize"/>
        </van-col>
        <van-col span="12">
